				cancelled: '订单已取消'
			},
			orderType: ['全部', '待付款', '待发货', '待收货', '已完成'],
			//订单列表 演示数据
			orderList: [],
			list: [],
			tabbarIndex: 0,
			array: ["我不想买了", "信息填写错误，重新拍", "卖家缺货", "其他原因"]
