		//去付款
		toPayment(order, index) {
			//调起支付接口
			var _this = this;
			uni.request({
				url: this.config.url + "order/pay",
				method: "POST",
				data: {
					token: this.token,
					order_no: order
				},
				success: (res) => {
					console.log(res)
					if (res.data.code == 1) {
						var pay = res.data.data.data
						uni.requestPayment({
							provider: 'wxpay',
							appid: pay.appId,
							timeStamp: pay.timeStamp,
							nonceStr: pay.nonceStr,
							package: pay.package,
							signType: pay.signType,
							paySign: pay.paySign,
							success: function(res) {
								console.log('success:' + JSON.stringify(res));
								_this.orderList.splice(index, 1)
								uni.showToast({
									title: "支付成功",

								})
							},
							fail: function(err) {
								console.log('fail:' + JSON.stringify(err));
								uni.showToast({
									title: "支付失败"
								})
							}
						});
					} else {
						uni.showToast({
							title: res.data.info
						})
					}
				}
			})

		},
		//取消订单
