		},
		//申请售后
		service(row) {
			uni.navigateTo({
				url: "pages/serType/serType"
			})
			uni.setStorage({
				key: "regoods",
				data: row,
				success: () => {
					console.log('保存换货数据成功')
				}
			})
		},
		//删除订单
		deleteOrder(order, index) {
			uni.request({
				url: this.config.url + "order/del",
				method: "POST",
				data: {
					token: this.token,
					order_no: order,
				},
				success: res => {
					console.log(res)
					this.orderList.splice(index, 1)
					if (res.data.code == 1) {
						uni.showToast({
							title: "删除订单成功"
						})
					}
				}
			})
		}
